// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import Vuetify from 'vuetify';
import 'vuetify/dist/vuetify.min.css'
import App from './App';
import {
  store
} from './store/store';
import router from './router/router';
import VueAwesomeSwiper from 'vue-awesome-swiper'
import 'swiper/dist/css/swiper.css'
import "./registerServiceWorker";

require('dotenv').config();

Vue.config.productionTip = false;

Vue.use(Vuetify, {
  iconfont: 'mdi'
});
Vue.use(VueAwesomeSwiper);

Vue.mixin({
  methods: {
    playbeep: function (sound) {
      if (this.$store.getters.sound && sound) {
        let audio = new Audio(sound);
        audio.play();
      }
    },
    getStates: function () {
      console.log('getter called with ' + this.$store.getters.ac_idx)
      let data = new FormData()
      data.append('ac_idx', this.$store.getters.ac_idx)
      fetch('https://www.hoopeeworld.co.kr/goldrush/getStates.php', {
          method: 'POST',
          body: data
        })
        .then(res => res.json())
        .then(data => {
          if (data.result == 0) {
            // 서버에서 점수를 가져올때는 무조건 state 초기화 개념이다.
            this.$store.commit('initScore', parseInt(data.data.score))
            this.$store.commit('setLevel', parseInt(data.data.level))
            this.$store.commit('setGemRoom', parseInt(data.data.room_no))
            this.$store.commit('setKey', parseInt(data.data.key_no))
            this.$store.commit('setOwner', data.data.owner_name)
            console.log(data.data)
          }
        })
    },
    setStates: function (flag) {
      let data = new FormData()
      data.append('ac_idx', this.$store.getters.ac_idx)
      data.append('score', this.$store.getters.score)
      // 미션에 성공한 경우에믄 새로운 방번호와 키번호를 보낸다. 서버에서는 이 두 필드에 모두 값이 있어야만 gr_gold 테이블을 업데이트한다.
      if (flag) {
        data.append('room_no', this.$store.getters.gemroom)
        data.append('key_no', this.$store.getters.key)
      }
      fetch('https://www.hoopeeworld.co.kr/goldrush/setStates.php', {
          method: 'POST',
          body: data
        })
        .then(res => {
          return res.json()
        })
        .then(data => {
          if (data.result == 0) {
            console.log('success')
          } else {
            console.log(data.message)
          }
        })
    },
  },
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  store,
  router,
  components: {
    App
  },
  template: '<App/>'
});
