<template>
  <v-img :src="require('@/assets/minimg/main.jpg')">
    <v-container v-if="ready" fill-height>
      <v-flex xs12>
        <v-layout column fill-height>
          <v-flex class="align-self-center justify-self-center mb-5">
            <v-img :src="require('@/assets/minimg/box.png')">
              <br>
              <div class="text-xs-center mt-5 pa-5 headline" wrap>
                현재 황금성의 주인은
                <br>
                <strong>{{ owner }}</strong>님 입니다.
              </div>
            </v-img>
          </v-flex>
          <v-flex class="justify-self-end align-self-center mb-0 pb-0">
            <v-btn large class="brown darken-3 yellow--text headline" @click="startGame">게임 시작</v-btn>
          </v-flex>
        </v-layout>
      </v-flex>
    </v-container>
  </v-img>
</template>

<script>
export default {
  name: "splash",
  data() {
    return {
      ready: false
    };
  },
  props: {
    ac_idx: {
      type: String
    }
  },
  created() {
    if (this.ac_idx) {
      localStorage.setItem("userId", this.ac_idx);

      this.$store.commit("setAcIdx", this.ac_idx);
      this.getStates();
      setTimeout(() => {
        this.ready = true;
      }, 1500);
    } else {
      let idx = localStorage.getItem("userId");
      if (idx) {
        this.$store.commit("setAcIdx", idx);
        this.getStates();
        setTimeout(() => {
          this.ready = true;
        }, 1500);
      } else {
        console.log("no idx");
      }
    }
  },
  computed: {
    owner() {
      return this.$store.getters.owner;
    }
  },
  methods: {
    startGame: function() {
      this.$router.push({ name: "game" });
    }
  }
};
</script>

<style>
.vertical-center {
  margin: 0;
  position: absolute;
  top: 40%;
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}
.vertical-bottom {
  margin: 0;
  position: absolute;
  top: 90%;
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}
</style>
