import Vue from 'vue';
import Router from 'vue-router';
import Splash from '../components/Splash.vue';

Vue.use(Router);

export default new Router({
  mode: 'history',
  routes: [{
      path: '/:ac_idx',
      name: 'splash',
      component: Splash,
      props: true
    },
    {
      path: '/game/:rand',
      name: 'game',
      component: () => import('../components/Game.vue')
    },
    {
      path: '/bonus/:rand',
      name: 'bonus',
      component: () => import('../components/Bonus.vue')
    },
  ]
});
