import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export const store = new Vuex.Store({
  state: {
    ac_idx: 0,
    level: 0,
    flag: false,
    sound: true,
    map: [
      // type -> 0: start, 1: words, -1~-4: blocks, >2: items, 99: goal
      // status -> 0: pending, 1: correct, -1: incorrect
      // clickable -> 0: false, -1: left, 1: right, 7: up
      {
        type: 0,
        status: 0,
        clickable: 0
      }
    ],
    wrong: 0,
    athena: 2,
    scanner: 1,
    owner: '',
    key: 0,
    score: 0,
    gemroom: 0 // 1: heart, 2: circle, 3: square
  },
  mutations: {
    initMap(state) {
      // 맵 초기화 하면서 직전판의 틀린 수도 0으로 리셋시켜 현재판에 반영 안 되도록 한다.
      state.wrong = 0
      state.map = [{
        type: 0,
        status: 0,
        clickable: 0
      }]
    },
    setAcIdx(state, newIdx) {
      state.ac_idx = newIdx
    },
    setLevel(state, newLevel) {
      state.level = newLevel
    },
    setFlag(state, newStatus) {
      state.flag = newStatus
    },
    setSound(state, newStatus) {
      state.sound = newStatus
      localStorage.setItem('soundOn', newStatus)
    },
    incrementWrong(state) {
      state.wrong += 1
    },
    setKey(state, newKey) {
      state.key = newKey
    },
    setGemRoom(state, newRoom) {
      state.gemroom = newRoom
    },
    initScore(state, score) {
      state.score = score
    },
    setScore(state, score) {
      state.score += score
    },
    setAthena(state, count) {
      state.athena += count
    },
    setScanner(state, count) {
      state.scanner += count
    },
    setOwner(state, newOwner) {
      state.owner = newOwner
    },
    changeStatus(state, {
      index,
      status
    }) {
      console.log('index ', index)
      // let obj = { type: state.map[index].type, status: status, clickable: 0 }
      // state.map.splice(index, 1, obj)
      state.map[index].status = status
      state.map[index].clickable = 0
      // or Vue.set(state.map, index, obj)
      if (status === 1) {
        state.map.forEach(element => {
          element.clickable = 0
        })
        // 정답이면 -1, +1, +7 위치의 clickable이 true가 되어야 한다. (단, type이 -1이면 그대로 둔다. 맵 세팅시 type이 -1이면 status를 -99로 해서 아래 체크로 충분)
        // index - 1이 아랫줄 마지막 컬럼으로 가는 것을 방지
        let idx = index - 1
        if ((idx > 0 && idx % 7 !== 6) && state.map[idx].status === 0) {
          state.map.splice(idx, 1, {
            type: state.map[idx].type,
            status: state.map[idx].status,
            clickable: -1
          })
        }
        // index + 1이 윗줄 첫 컬럼으로 가는 것을 방지
        idx = index + 1
        if (idx % 7 != 0 && state.map[idx].status === 0) {
          state.map.splice(idx, 1, {
            type: state.map[idx].type,
            status: state.map[idx].status,
            clickable: 1
          })
        }
        idx = index + 7
        if (idx < state.map.length - 1 && state.map[idx].status === 0) {
          state.map.splice(idx, 1, {
            type: state.map[idx].type,
            status: state.map[idx].status,
            clickable: 7
          })
        }
      }
    }
  },
  getters: {
    ac_idx: (state) => state.ac_idx,
    level: (state) => state.level,
    flag: (state) => state.flag,
    sound: (state) => {
      return state.sound
    },
    wrong: (state) => state.wrong,
    owner: (state) => state.owner,
    gemroom: (state) => state.gemroom,
    key: (state) => state.key,
    score: (state) => state.score,
    athena: (state) => state.athena,
    scanner: (state) => state.scanner,
    status: (state) => (index) => state.map[index].status,
    type: (state) => (index) => state.map[index].type,
    clickable: (state) => (index) => state.map[index].clickable,
    movable: (state) => state.map.filter(cell => cell.clickable !== 0).length
  }
})
